        "title":"Http Get Method",
        "description":"<p>In Express you define routing using methods of the express object. One of the available methods is <strong style='color:blue'>app.get()</strong> that handles requests to the HTTP GET method. This method takes a string as the first argument and a callback function that takes a request and response arguments.<br/><strong><p>Eg.</p></strong><CodeBlock>app.get('route', (request, response) => {\r\n  response.send('text');\r\n});<CodeBlock>In this challenge you are required to implement a <strong style='color:blue'>GET</strong> method endpoint where the client should receive a response with the text <strong style='color:blue'>Home Page</strong> (same capitalization). We have already started an express app and you are to use the <span style='color:blue'>app.get()</span> function.</p>\r\n",
        "sampleAnswer":"app.get(\"/\", (request, response) => {\r\n    res.send(\"Home Page\");\r\n});",
